<template>
  <v-container>
    <section>
      <div @click="gocategory" class="back_menu"><i class="fas fa-arrow-circle-left fa-1x" style="margin:5px;"></i>{{ koreanlist[koreanno].cate }}</div>
    </section>
    <div v-if="tokki==1">  <!--  -->
      <h3>!   !</h3>
      <v-img src="../../assets/images/good.jpg" max-height="30%" max-width="15%" style="float:right; margin:11% auto;"></v-img>
      <v-img src="../../assets/images/good2.jpg" max-height="30%" max-width="15%" style="float:left; margin:11% auto;"></v-img>
    </div>
    <div v-if="tokki==2"> <!--  -->
      <h3>  ?</h3>
      <v-img src="../../assets/images/next.jpg" max-height="30%" max-width="15%" style="float:left; margin:11% auto;"></v-img>
      <v-img src="../../assets/images/next.jpg" max-height="30%" max-width="15%" style="float:right; margin:11% auto%;"></v-img>
    </div>
    <div v-if="tokki==3"> <!--  -->
      <h3> ?</h3>
      <v-img src="../../assets/images/nono.jpg" max-height="30%" max-width="15%" style="float:left; margin:11% auto;"></v-img>
      <v-img src="../../assets/images/nono.jpg" max-height="30%" max-width="15%" style="float:right; margin:11% auto;"></v-img>
    </div>
    <div v-if="tokki==4"> <!--  -->
      <h3> ?</h3>
      <v-img src="../../assets/images/question2.jpg" max-height="30%" max-width="15%" style="float:left; margin:11% auto;"></v-img>
      <v-img src="../../assets/images/question.jpg" max-height="30%" max-width="15%" style="float:right; margin:11% auto;"></v-img>
    </div>
    <v-row>
      <v-col>
        <div>
          <alphabet-writing v-on:success="test" @speak="speak" ref="canvas" v-if="flag" :letter="koreanlist[koreanno].name"></alphabet-writing>
          <v-row class="prev_next">
            <v-col>
              <template v-if="koreanno == 13 || koreanno == 23 || koreanno == 28 || koreanno == 39 || koreanno == 63">
                <i @click="behind" style="float:right" class="fas fa-angle-left fa-5x"></i>
              </template>
              <template v-else-if="koreanno < 63">
                <i @click="behind" style="float:right" class="fas fa-angle-left fa-5x"></i>
              </template>
            </v-col>
            <v-col>
              <v-img src="../../assets/images/whale.png" style="cursor:pointer" @click="speak" aspect-ratio="1" class="img-whale">
                <div class="whale-content">
                  <h2>{{koreanlist[koreanno].text}}</h2>
                </div>
              </v-img>
            </v-col>
            <v-col>
              <template v-if="koreanno == 13 || koreanno == 23 || koreanno == 28 || koreanno == 39 || koreanno == 63">
                <i @click="finish" style="float:left" class="fas fa-angle-right fa-5x"></i>
              </template>
              <template v-else-if="koreanno < 63">
                <i @click="next" style="float:left" class="fas fa-angle-right fa-5x"></i>
              </template>
            </v-col>
          </v-row>
        </div>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import AlphabetWriting from '@/components/AlphabetWriting'

export default {
  name: 'koreandetail',
  components:{'AlphabetWriting':AlphabetWriting},
  data(){
    return {
      tokki : 4,
      flag: true,
      namelist: ['', '', '', '', ''],
      koreanno : 0,
      koreanlist: [
        { id:"0", name: '', text: '', cate: '' }, 
        { id:"1", name: '', text: '', cate: '' }, 
        { id:"2", name: '', text: '', cate: '' }, 
        { id:"3", name: '', text: '', cate: '' }, 
        { id:"4", name: '', text: '', cate: '' }, 
        { id:"5", name: '', text: '', cate: '' }, 
        { id:"6", name: '', text: '', cate: '' }, 
        { id:"7", name: '', text: '', cate: '' }, 
        { id:"8", name: '', text: '', cate: '' }, 
        { id:"9", name: '', text: '', cate: '' }, 
        { id:"10", name: '', text: '', cate: '' }, 
        { id:"11", name: '', text: '', cate: '' }, 
        { id:"12", name: '', text: '', cate: '' }, 
        { id:"13", name: '', text: '', cate: '' }, 
        { id:"14", name: '', text: '', cate: '' }, 
        { id:"15", name: '', text: '', cate: '' }, 
        { id:"16", name: '', text: '', cate: '' }, 
        { id:"17", name: '', text: '', cate: '' }, 
        { id:"18", name: '', text: '', cate: '' }, 
        { id:"19", name: '', text: '', cate: '' }, 
        { id:"20", name: '', text: '', cate: '' }, 
        { id:"21", name: '', text: '', cate: '' }, 
        { id:"22", name: '', text: '', cate: '' }, 
        { id:"23", name: '', text: '', cate: '' }, 
        { id:"24", name: '', text: '', cate: '' }, 
        { id:"25", name: '', text: '', cate: '' }, 
        { id:"26", name: '', text: '', cate: '' }, 
        { id:"27", name: '', text: '', cate: '' }, 
        { id:"28", name: '', text: '', cate: '' }, 
        { id:"29", name: '', text: '', cate: '' }, 
        { id:"30", name: '', text: '', cate: '' }, 
        { id:"31", name: '', text: '', cate: '' }, 
        { id:"32", name: '', text: '', cate: '' }, 
        { id:"33", name: '', text: '', cate: '' }, 
        { id:"34", name: '', text: '', cate: '' }, 
        { id:"35", name: '', text: '', cate: '' },
        { id:"36", name: '', text: '', cate: '' }, 
        { id:"37", name: '', text: '', cate: '' }, 
        { id:"38", name: '', text: '', cate: '' }, 
        { id:"39", name: '', text: '', cate: '' },
        { id:"40", name: '', text: '', cate: '' }, 
        { id:"41", name: '', text: '', cate: '' }, 
        { id:"42", name: '', text: '', cate: '' }, 
        { id:"43", name: '', text: '', cate: '' }, 
        { id:"44", name: '', text: '', cate: '' }, 
        { id:"45", name: '', text: '', cate: '' }, 
        { id:"46", name: '', text: '', cate: '' }, 
        { id:"47", name: '', text: '', cate: '' }, 
        { id:"48", name: '', text: '', cate: '' }, 
        { id:"49", name: '', text: '', cate: '' }, 
        { id:"50", name: '', text: '', cate: '' }, 
        { id:"51", name: '', text: '', cate: '' }, 
        { id:"52", name: '', text: '', cate: '' }, 
        { id:"53", name: '', text: '', cate: '' }, 
        { id:"54", name: '', text: '', cate: '' }, 
        { id:"55", name: '', text: '', cate: '' }, 
        { id:"56", name: '', text: '', cate: '' }, 
        { id:"57", name: '', text: '', cate: '' }, 
        { id:"58", name: '', text: '', cate: '' }, 
        { id:"59", name: '', text: '', cate: '' }, 
        { id:"60", name: '', text: '', cate: '' }, 
        { id:"61", name: '', text: '', cate: '' }, 
        { id:"62", name: '', text: '', cate: '' }, 
        { id:"63", name: '', text: '', cate: '' }, 
      ],
    }
  },
  created(){
    this.koreanno = this.$route.params.koreanno
    for(let i in this.koreanlist){
      if(this.koreanlist[i].id == this.koreanno){
        this.koreanno = i
        break
      }
    }
  },
  methods: {
    speak(){
      new Audio(`${this.$store.getters.BACKEND_URL}/letter/${this.koreanlist[this.koreanno].name}.mp3`).play()
    },
    gocategory() {
      if(this.koreanno >= 0 && this.koreanno <= 13){
        this.$router.push('/koreanconsonant')
      }
      if(this.koreanno >= 14 && this.koreanno <= 23){
        this.$router.push('/koreanvowel')
      }
      if(this.koreanno >= 24 && this.koreanno <= 28){
        this.$router.push('/koreandoubleconsonant')
      }
      if(this.koreanno >= 29 && this.koreanno <= 39){
        this.$router.push('/koreandoublevowel')
      }
      if(this.koreanno >= 40 && this.koreanno <= 63){
        this.$router.push('/koreanletter')
      }
    },
    recreate(){
      this.flag = false;
      let timerId = setInterval(()=>{
        if(!this.$refs.canvas){
          clearInterval(timerId)
          this.flag = true;
        }
      },100)
      
    },
    next() {
      this.koreanno = parseInt(this.koreanno) + parseInt(1)
      this.$router.push(`/koreandetail/${this.koreanno}`)
      this.recreate()
      this.tokki = 4
    },
    behind() {
      if(this.koreanno == 0){
        return;
      }
      if(this.koreanno == 14){
       return;
      }
      if(this.koreanno == 24){
       return;
      }
      if(this.koreanno == 29){
        return;
      }
      if(this.koreanno == 40){
       return;
      }
      this.koreanno = this.koreanno - 1
      this.recreate()
    },
    finish() {
      if(this.koreanno == 13){
        this.$router.push('/alphabet')
        alert('!  !')
      }
      if(this.koreanno == 23){
        this.$router.push('/alphabet')
        alert('!  !')
      }
      if(this.koreanno == 28){
        this.$router.push('/alphabet')
        alert('!  !')
      }
      if(this.koreanno == 39){
        this.$router.push('/alphabet')
        alert('!  !')
      }
      if(this.koreanno == 63){
        this.$router.push('/alphabet')
        alert('!  !')
      }
    },
    test(val){
      this.tokki = val
    }
  }
}
</script>

